@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor-app {
	@include m.tablet {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		min-height: 100vh;
	}

	.imagen {
		height: 20rem;
		background-image: url("../img/1.jpg");
		background-size: cover;
		background-position: center center;
		@include m.tablet {
			height: auto;
		}
	}
	.app {
		width: 95%;
		margin: 0 auto;
		padding: 3rem 0;
		@include m.tablet {
			overflow: scroll;
			// Ocultar barra inferior
			overflow-x: hidden;

			// Dando estilos a la barra lateal
			&::-webkit-scrollbar {
				width: 0.9rem;
			}

			&::-webkit-scrollbar-thumb {
				background-color: rgba(255, 255, 255, 0.4);
				border-radius: 5px;
			}
		}
	}
}

.barra {
	margin: v.$separacion 0;

	@include m.tablet {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	p {
		font-weight: v.$regular;

		span {
			font-weight: v.$bold;
		}
	}
	.boton {
		@include m.boton(v.$negro, v.$blanco);
		& {
			margin: 0;
			border-radius: 1rem;
		}
	}
}
