@use "base/variables" as v;
@use "base/mixins" as m;

.formulario {
	margin-top: 5rem;
	width: 98%;
}

.campo {
	display: flex;
	gap: 1.2rem;
	margin-bottom: 2rem;
	align-items: center;
	label {
		flex: 0 0 10rem;
	}
	input {
		flex: 1;
		border: none;
		padding: calc(v.$separacion / 4);
		border-radius: 1rem;

		&:disabled {
			background-color: lighten(v.$negro, 20%);
			color: v.$blanco;
			cursor: not-allowed;
		}
	}
}
