@use "base/variables" as v;
@use "base/mixins" as m;

.acciones {
	margin-top: v.$separacion;

	@include m.tablet {
		display: flex;
		justify-content: space-around;
		align-items: center;
	}
	a {
		color: v.$blanco;
		display: block;
		margin-bottom: calc(v.$separacion / 2);
		text-align: center;
		transition: ease-in-out 300ms;

		&:hover {
			color: v.$azul;
		}
	}
}
